import {
    signInWithPopup,
    GoogleAuthProvider,
    createUserWithEmailAndPassword,
    signInWithEmailAndPassword,
    signOut,
    sendPasswordResetEmail,
    updateProfile,
    User,
    UserCredential
} from 'firebase/auth';
import { auth, setPersistence, browserLocalPersistence, browserSessionPersistence } from './firebase';

// Google Provider
const googleProvider = new GoogleAuthProvider();

/**
 * Google ile giri≈ü yap
 */
export async function signInWithGoogle(rememberMe: boolean = false): Promise<UserCredential> {
    try {
        // Persistence ayarla
        await setPersistence(
            auth,
            rememberMe ? browserLocalPersistence : browserSessionPersistence
        );

        // Google popup ile giri≈ü
        const result = await signInWithPopup(auth, googleProvider);
        return result;
    } catch (error: any) {
        console.error('Google sign-in error:', error);
        throw new Error(getAuthErrorMessage(error.code));
    }
}

/**
 * Email ve ≈üifre ile kayƒ±t ol
 */
export async function signUpWithEmail(
    email: string,
    password: string,
    displayName: string,
    rememberMe: boolean = false
): Promise<UserCredential> {
    try {
        // Persistence ayarla
        await setPersistence(
            auth,
            rememberMe ? browserLocalPersistence : browserSessionPersistence
        );

        // Kullanƒ±cƒ± olu≈ütur
        const result = await createUserWithEmailAndPassword(auth, email, password);

        // Profil bilgilerini g√ºncelle
        if (result.user) {
            await updateProfile(result.user, { displayName });
        }

        return result;
    } catch (error: any) {
        console.error('Sign-up error:', error);
        throw new Error(getAuthErrorMessage(error.code));
    }
}

/**
 * Email ve ≈üifre ile giri≈ü yap
 */
export async function signInWithEmail(
    email: string,
    password: string,
    rememberMe: boolean = false
): Promise<UserCredential> {
    try {
        // Persistence ayarla
        await setPersistence(
            auth,
            rememberMe ? browserLocalPersistence : browserSessionPersistence
        );

        // Giri≈ü yap
        const result = await signInWithEmailAndPassword(auth, email, password);
        return result;
    } catch (error: any) {
        console.error('Sign-in error:', error);
        throw new Error(getAuthErrorMessage(error.code));
    }
}

/**
 * √áƒ±kƒ±≈ü yap
 */
export async function signOutUser(): Promise<void> {
    try {
        await signOut(auth);
    } catch (error: any) {
        console.error('Sign-out error:', error);
        throw new Error('√áƒ±kƒ±≈ü yapƒ±lƒ±rken bir hata olu≈ütu.');
    }
}

/**
 * ≈ûifre sƒ±fƒ±rlama emaili g√∂nder
 */
export async function resetPassword(email: string): Promise<void> {
    try {
        await sendPasswordResetEmail(auth, email);
    } catch (error: any) {
        console.error('Password reset error:', error);
        throw new Error(getAuthErrorMessage(error.code));
    }
}

/**
 * Kullanƒ±cƒ± profil bilgilerini g√ºncelle
 */
export async function updateUserProfile(
    user: User,
    profile: { displayName?: string; photoURL?: string }
): Promise<void> {
    try {
        await updateProfile(user, profile);
    } catch (error: any) {
        console.error('Profile update error:', error);
        throw new Error('Profil g√ºncellenirken bir hata olu≈ütu.');
    }
}

